import middle from '../../../img/quiz/middle.png';

import {
  BackgroundFilterBottomLeft,
  BackgroundFilterTopRight,
  BackgroungStarSmall,
  CurrentPage,
  InterestsEmoji,
  Logo,
  NextPageBtn,
  PageCounter,
  Pagination,
  PreviousPageBtn,
  Question,
  QuizArrowLeft,
  QuizArrowRight,
  QuizBox,
  QuizButton,
  QuizButtonBox,
  QuizButtonContent,
} from '../Quiz.styled';

export const QuizQuestionInterests = ({
  activeSlide,
  continueQuiz,
  previousQuestion,
  nextQuestion,
  quizValues,
  lang
}) => {
  const setQuizValue = (e, value) => {
    quizValues.current.interests = value;
    continueQuiz(e);
  };

  return (
    <>
      <QuizBox>
        <Logo />
        <Question>
          –©–æ –≤–∞—Å –∑–∞—Ä–∞–∑ —Ü—ñ–∫–∞–≤–∏—Ç—å —É –≤–∏–≤—á–µ–Ω–Ω—ñ{' '}
          {quizValues.current.lang === 'en'
            ? '–∞–Ω–≥–ª—ñ–π—Å—å–∫–æ—ó'
            : quizValues.current.lang === 'de'
            ? '–Ω—ñ–º–µ—Ü—å–∫–æ—ó'
            : quizValues.current.lang === 'pl'
            ? '–ø–æ–ª—å—Å—å–∫–æ—ó'
            : '—ñ–Ω–æ–∑–µ–º–Ω–æ—ó'}{' '}
          –º–æ–≤–∏?
          <InterestsEmoji src={middle} alt="Alumni hat emoji" />
        </Question>
        <QuizButtonBox>
          <QuizButton
            onClick={e => setQuizValue(e, '–†–æ–∑—à–∏—Ä–∏—Ç–∏ —Å–ª–æ–≤–Ω–∏–∫–æ–≤–∏–π –∑–∞–ø–∞—Å')}
            className={
              quizValues.current?.interests === '–†–æ–∑—à–∏—Ä–∏—Ç–∏ —Å–ª–æ–≤–Ω–∏–∫–æ–≤–∏–π –∑–∞–ø–∞—Å' &&
              'chosen'
            }
          >
            <QuizButtonContent>–í–∏–≤—á–∏—Ç–∏ —Å–ª–æ–≤–∞</QuizButtonContent>
          </QuizButton>
          <QuizButton
            onClick={e => setQuizValue(e, '–í–∏–≤—á–∏—Ç–∏ –≥—Ä–∞–º–∞—Ç–∏–∫—É')}
            className={
              quizValues.current?.interests === '–í–∏–≤—á–∏—Ç–∏ –≥—Ä–∞–º–∞—Ç–∏–∫—É' && 'chosen'
            }
          >
            <QuizButtonContent>–í–∏–≤—á–∏—Ç–∏ –≥—Ä–∞–º–∞—Ç–∏–∫—É</QuizButtonContent>
          </QuizButton>
          <QuizButton
            onClick={e => setQuizValue(e, '–í—ñ–ª—å–Ω–æ —Ä–æ–∑–º–æ–≤–ª—è—Ç–∏')}
            className={
              quizValues.current?.interests === '–í—ñ–ª—å–Ω–æ —Ä–æ–∑–º–æ–≤–ª—è—Ç–∏' && 'chosen'
            }
          >
            <QuizButtonContent>–í—ñ–ª—å–Ω–æ —Ä–æ–∑–º–æ–≤–ª—è—Ç–∏</QuizButtonContent>
          </QuizButton>
          <QuizButton
            onClick={e => setQuizValue(e, '–í—Å–µ —Ä–∞–∑–æ–º')}
            className={
              quizValues.current?.interests === '–í—Å–µ —Ä–∞–∑–æ–º' && 'chosen'
            }
          >
            <QuizButtonContent>–í—Å–µ —Ä–∞–∑–æ–º</QuizButtonContent>
          </QuizButton>
        </QuizButtonBox>
        <BackgroundFilterTopRight />
        <BackgroundFilterBottomLeft />
        <BackgroungStarSmall />
        <Pagination>
          <PreviousPageBtn
            className={activeSlide - 1 < 1 && 'disabled'}
            disabled={activeSlide - 1 < 1 && true}
            onClick={previousQuestion}
          >
            <QuizArrowLeft />
          </PreviousPageBtn>
          <PageCounter>
            <CurrentPage>{activeSlide}</CurrentPage>/{lang ? 7 : 8}
          </PageCounter>
          <NextPageBtn
            className={
              activeSlide + 1 > 1 &&
              !quizValues.current?.interests &&
              'disabled'
            }
            disabled={
              activeSlide + 1 > 1 && !quizValues.current?.interests && true
            }
            onClick={nextQuestion}
          >
            <QuizArrowRight />
          </NextPageBtn>
        </Pagination>
      </QuizBox>
    </>
  );
};
